section{
  height: 100vh;
  width: 100vw;
  overflow: hidden;
}


.flex-center{
  display: flex;
  justify-content: center;
  align-items: center;
}

.flex-col{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.padContainer{
  height: 100vh;
  width: 100vw;
  background: #ededed;
  box-shadow: 0px 0px 10px -2px #333333;
  display: grid;
  grid-template-rows: 2fr 7fr 1.5fr;
  padding: 10px;
}

.title{
  width: -webkit-fill-available;
  height: auto;
  color: #333333;
  font-size: 4rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 5px;
  opacity: 0.3;
}

.pad{
  background: #ffffff; 
  height: 78%;
  width: 420px;
  border-radius: 3px;
  box-shadow: 0px 0px 5px 0px #33333333;
  padding: 10px;
  display: grid;
  grid-template-rows: 88% 10%;
  grid-gap: 10px;
}

.footer{
  width: -webkit-fill-available;
  height: auto;
  color: #333333;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 5px;
  opacity: 0.3;
}

.grid{
  display: grid;
  grid-template-columns: repeat(28,1fr);
  grid-template-rows: repeat(28,1fr);
  height: 400px;
  width: 400px;
  cursor: crosshair;
}

.tools{
  display: grid;
  grid-template-columns:1fr 1fr 1fr 4fr;
  width: 400px;
  grid-gap: 5px;
}

.tool{
  padding: 2px;
  background: #f4f4f4;
  border-radius: 2px;
  border: solid 0.04rem rgba(0, 0, 0, 0.2);
  cursor: pointer;
}

.cell-0{
  border: solid 0.001rem rgba(0, 0, 0, 0.2);
}

.cell-1{
  border: solid 0.001rem rgba(0, 0, 0, 0.2);
  background: #333333;
}

.output{
  letter-spacing: 1px;
  text-transform: uppercase;
  font-size: 0.9rem;
  font-weight: 500;
  border: solid 0.04rem rgba(0, 0, 0, 0.2);
  color: #333333;
  opacity: 0.8;
}

svg{
  height: 28px;
  width: 28px;
}